* {
  box-sizing: border-box;
}

:root {
  --lg: 2.25rem;
}

@media (max-width: 992px) {
  :root {
    --lg: 1.5rem;
  }
}
html {
  height: 100%;
}

body {
  font: 16px/1.4 "Jost", sans-serif;
  color: #2f2c26;
  margin: 0;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
}

audio,
canvas,
img,
video {
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
  vertical-align: top;
}

img {
  border-style: none;
  max-width: 100%;
  display: inline-block;
  outline: none !important;
}

a {
  color: #6682d4;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

svg:not(:root) {
  overflow: hidden;
}

svg {
  fill: currentcolor;
  max-width: 100%;
}

.link-block {
  text-decoration: none;
  color: inherit;
}

.hidden {
  display: none;
}

.disable {
  opacity: 0.5;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.svg-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
}

.contact {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  color: inherit;
}
.contact:hover {
  color: inherit;
}
.contact__ico {
  font-size: 26px;
}
.contact__cnt {
  margin-left: 0.5em;
}
.contact__pre {
  font-size: 14px;
}
.contact__txt {
  font-weight: 500;
  font-size: 18px;
}

@media (max-width: 540px) {
  .header__contacts--tel {
    margin-left: auto;
  }
  .header__contacts--tel .contact__ico {
    background-color: #6682d4;
    color: #fff;
    font-size: 28px;
  }
  .header__contacts--tel .contact__cnt {
    display: none;
  }
}

.section-intro {
  margin-bottom: var(--lg);
}
.section-intro .title-section {
  align-self: flex-start;
}
@media (min-width: 767px) {
  .section-intro {
    display: grid;
    gap: calc(0.5 * var(--lg)) var(--lg);
    align-items: end;
    grid-template-columns: 1fr 1fr;
    margin-bottom: calc(2 * var(--lg));
  }
  .section-intro p {
    font-size: 18px;
    margin: 0 0 0.5em;
  }
}
.section-intro--title-line {
  align-items: start;
}
.section-intro--title-line .title-section {
  grid-column: span 2;
  margin-bottom: 2rem;
}

.rating span {
  display: inline-block;
  width: 1em;
  height: 1em;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/star-blue.svg");
}

.rating[data-rating="4"] span:nth-child(5) {
  opacity: 0.3;
}

.rating[data-rating="3"] span:nth-child(5),
.rating[data-rating="3"] span:nth-child(4) {
  opacity: 0.3;
}

.rating[data-rating="2"] span:nth-child(5),
.rating[data-rating="2"] span:nth-child(4),
.rating[data-rating="2"] span:nth-child(3) {
  opacity: 0.3;
}

.rating[data-rating="1"] span:nth-child(5),
.rating[data-rating="1"] span:nth-child(4),
.rating[data-rating="1"] span:nth-child(3),
.rating[data-rating="1"] span:nth-child(2) {
  opacity: 0.3;
}

.review {
  border: 1px solid #eceef2;
  padding: var(--lg);
  display: flex;
  flex-direction: column;
}
.review__header {
  display: flex;
  gap: 10px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2em;
}
.review__header .rating span {
  font-size: 16px;
  background-image: url("../img/star-blue.svg");
}
.review__name {
  font-weight: 500;
  font-size: 16px;
}
@media (min-width: 992px) {
  .review__name {
    font-size: 22px;
  }
}
.review__footer {
  margin-top: auto;
}
.review__more {
  text-decoration: none;
  color: inherit;
  font-weight: 500;
}
.review--banner .review__header {
  margin: 1em 0 0;
}
.review--banner .rating span {
  font-size: 16px;
  background-image: url("../img/star-white.svg");
}

.reviews {
  display: grid;
  gap: var(--lg);
}
@media (min-width: 540px) {
  .reviews {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 767px) {
  .reviews {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1400px) {
  .reviews {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 992px) {
  .reviews--index .review:nth-child(3n+1) {
    grid-column: span 2;
  }
}

input:not([type=submit]),
input:not([type=checkbox]),
input:not([type=radio]),
textarea {
  padding: 1em 2em;
  border-radius: 100px;
  border: none;
  display: block;
}

input:focus,
textarea:focus {
  outline: none;
  box-shadow: 0 0 5px rgba(102, 130, 212, 0.5);
}
input:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  opacity: 0.5;
}
input:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  opacity: 0.5;
}
input:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  opacity: 0.5;
}
input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  opacity: 0.5;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0.5;
}

.form {
  max-width: 650px;
  margin: 0 auto;
}

.form-line {
  position: relative;
  margin-bottom: calc(0.5 * var(--lg));
}
.form-line input:not([type=checkbox]):not([type=radio]):not([type=submit]) {
  width: 100%;
}
.form-line--inline {
  display: flex;
  gap: var(--lg);
}
@media (max-width: 540px) {
  .form-line--inline {
    flex-direction: column;
    align-items: center;
  }
}

.form__title {
  font-family: "Prata", serif;
  font-size: 24px;
  line-height: 1.2;
  margin: 0 0 0.5em;
}
@media (min-width: 540px) {
  .form__title {
    font-size: 40px;
  }
}

.form__txt {
  margin: 0 0 2.5em;
}
@media (min-width: 540px) {
  .form__txt {
    font-size: 18px;
  }
}

.form__footer {
  margin: 0;
}

.popup-form {
  display: none;
  max-width: 500px;
}

.btn {
  display: inline-flex;
  flex: 0 0 auto;
  font-weight: 500;
  padding: 1em 1em 1em;
  text-decoration: none;
  line-height: 1;
  border-radius: 0.5em;
  border: none;
  appearance: none;
  color: #2f2c26;
  position: relative;
  overflow: hidden;
  background: #e1e5f1;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
}
.btn:not(.btn--title):hover {
  cursor: pointer;
  box-shadow: 0 0 0.8em rgba(47, 44, 38, 0.3);
}
.btn--title {
  font-size: 16px;
  font-weight: 400;
  color: #6682d4;
  padding: 0.5em 1.5em;
  margin: 20px 0 20px;
}
@media (min-width: 540px) {
  .btn--title {
    margin: 0 0 50px;
  }
}
@media (max-width: 480px) {
  .btn--title {
    font-size: 12px;
  }
}
.btn--long {
  padding: 1em 3em;
}
.btn--big {
  padding: 1em 2em;
}
@media (min-width: 480px) {
  .btn--big {
    padding: 1.5em 3em;
  }
}
.btn--icon {
  height: 60px;
}
.btn--dark {
  background-color: #6682d4;
  color: #fff;
}
.btn--dark:hover {
  color: #fff;
}

.btn-wrap--center {
  text-align: center;
}

.form-call-cnt {
  text-align: center;
  background: #eceef2 url("../img/bg/bg_form_call.png") no-repeat bottom center;
}

.form-call-cnt.form-call-dark {
  background: #6682d4 url("../img/bg/bg_form_call-dark.png") no-repeat bottom center;
  color: #fff;
}

.form-call-dark .ico--round {
  background-color: #eceef2;
}
.form-call-dark .contact__cnt {
  text-align: left;
}
.form-call-dark .form-line--inline {
  justify-content: center;
  margin-bottom: 0;
}

.form {
  padding: var(--lg);
}
.form--center {
  text-align: center;
}
.form--center input {
  text-align: center;
}
.form--aside .form__title {
  font-size: 32px;
}
.form--aside .form__txt {
  font-size: 16px;
}
.form--aside .form__footer {
  font-size: 14px;
}
.form--aside [type=submit] {
  width: 100%;
}

.form--dark {
  background: #6682d4 url("../img/bg/bg_form_call-dark.png") no-repeat bottom center;
  color: #fff;
}

.nav {
  margin: auto;
  justify-content: center;
}
.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav__item {
  position: relative;
  transition: all 0.3s;
}
.nav__link {
  font-family: "Prata", serif;
  display: block;
  text-decoration: none;
  color: #2f2c26;
}
.nav__link:hover {
  color: #6682d4;
}

@media (min-width: 1025px) and (max-width: 1590px) {
  .header {
    flex-wrap: wrap;
  }

  .nav {
    order: 10;
    margin-top: var(--lg);
  }

  .nav > .nav__list > .nav__item > .nav__link {
    font-size: 18px;
  }
}
@media (min-width: 992px) {
  .nav > .nav__list {
    display: flex;
  }

  .nav > .nav__list > .nav__item > .nav__link {
    padding: 25px clamp(0.75em, 1vw, 2em);
  }

  .nav > .nav__list > .nav__item-sub > ul {
    padding: 20px;
    position: absolute;
    left: -50px;
    z-index: 100;
    background-color: #fff;
    filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.15));
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s 0.3s;
    min-width: 280px;
  }
  .nav > .nav__list > .nav__item-sub > ul:before {
    content: "";
    width: 30px;
    height: 30px;
    border-radius: 4px;
    transform: rotate(45deg);
    position: absolute;
    top: -15px;
    left: 75px;
    background-color: #fff;
  }

  .nav > .nav__list > .nav__item-sub:hover > ul {
    opacity: 1;
    visibility: visible;
  }

  .nav__list-sub > .nav__item {
    padding: 20px;
    border-radius: 20px;
  }

  .nav__list-sub > .nav__item:hover {
    background-color: #eceef2;
  }
  .nav__list-sub > .nav__item:hover > .nav__link {
    color: #2f2c26;
  }

  .nav > .nav__list > .nav__item-sub > ul > li a {
    font-size: 18px;
  }
  .nav > .nav__list > .nav__item-sub > ul > li ul {
    margin-top: 0.5em;
  }
  .nav > .nav__list > .nav__item-sub > ul > li ul > li > a {
    font: 400 14px "Jost", serif;
    color: #9BA1B3;
  }
  .nav > .nav__list > .nav__item-sub > ul > li ul > li > a:hover {
    color: #2f2c26;
  }
}
@media (min-width: 992px) {
  .nav-mob-only {
    display: none;
  }

  .nav__list-sub {
    display: grid;
    padding: 20px;
    border-radius: 20px;
  }

  .nav-mega--3 {
    grid-template-columns: repeat(3, 1fr);
  }

  .nav-mega--4 {
    grid-template-columns: repeat(4, 1fr);
  }

  .nav-mega {
    width: 75vw;
    max-width: 920px;
  }
  .nav-mega .service-card {
    background-color: transparent;
  }
  .nav-mega .service-card__ico {
    font-size: 32px;
    margin-bottom: 22px;
    background-color: #e1e5f1;
  }
  .nav-mega .service-card__title {
    font-size: 18px;
    margin-top: 0;
    color: #2f2c26;
  }
  .nav-mega .service-card__txt {
    font: 14px "Jost", sans-serif;
    margin-top: 0.75em;
    color: #2f2c26;
  }

  .nav-banner {
    grid-column: span 3;
  }
}
.burger {
  padding: 10px;
  display: inline-block;
  margin-left: var(--lg);
  cursor: pointer;
}
@media (min-width: 1024px) {
  .burger {
    display: none;
  }
}
.burger__el {
  position: relative;
  display: block;
  height: 2px;
  width: 30px;
  background-color: #6682d4;
}
.burger__el:before, .burger__el:after {
  content: "";
  position: absolute;
  height: 2px;
  width: 30px;
  background-color: #6682d4;
  transition: all 0.3s;
  transform-origin: 50% 50%;
}
.burger__el:before {
  transform: translateY(-8px);
}
.burger__el:after {
  transform: translateY(8px);
}
.burger.toggle-open .burger__el {
  background-color: transparent;
}
.burger.toggle-open .burger__el:before {
  transform: rotate(-45deg);
}
.burger.toggle-open .burger__el:after {
  transform: rotate(45deg);
}

.nav-banner a {
  color: #2f2c26;
  text-decoration: none;
}

@media (max-width: 992px) {
  .nav-desktop-only {
    display: none !important;
  }
}

.hc-offcanvas-nav li.nav-parent .nav-item-link:last-child {
  padding: 20px;
}

.hc-offcanvas-nav .service-card {
  display: flex !important;
  align-items: center;
  flex-direction: row;
  gap: 20px;
}
.hc-offcanvas-nav .service-card__ico {
  font-size: 20px;
}
.hc-offcanvas-nav .service-card__cnt,
.hc-offcanvas-nav .service-card__title {
  margin-top: 0;
  font-size: 16px;
}

.inner-nav {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 30px 0 0;
}
@media (min-width: 767px) {
  .inner-nav--right {
    margin-top: 0;
    grid-column-start: 2;
  }
}
.inner-nav__item:not(:last-child) {
  margin-right: 34px;
}
.inner-nav__link {
  text-decoration: none;
  color: #2f2c26;
  font-weight: 500;
  transition: color 0.2s;
}
.inner-nav__link:hover {
  color: #6682d4;
}

.nav-catalog {
  margin: calc(2 * var(--lg)) 0 0;
  padding: 0;
  display: flex;
  list-style: none;
  border-bottom: 1px solid #eceef2;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.nav-catalog::-webkit-scrollbar {
  display: none;
}
.nav-catalog__item {
  flex: 0 0 auto;
}
.nav-catalog__item:not(:first-child) {
  margin-left: 2em;
}
.nav-catalog__item.active .nav-catalog__link {
  border-color: #6682d4;
}
.nav-catalog__link {
  display: inline-block;
  text-decoration: none;
  font-weight: 500;
  color: #2f2c26;
  padding-bottom: 1em;
  border-bottom: 4px solid transparent;
  white-space: nowrap;
}

pre {
  font-family: inherit;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

* {
  min-width: 0;
}

a {
  transition: all 0.3s;
}

p:first-child {
  margin-top: 0;
}

.page {
  flex: 1 0;
  overflow: hidden;
}

.container,
[class*=_container] {
  max-width: 1840px;
  margin: 0 auto;
  width: 100%;
  padding: 0 15px;
}
@media (min-width: 1280px) {
  .container,
[class*=_container] {
    padding: 0 60px;
  }
}

a[href*=tel] {
  text-decoration: none;
  color: inherit;
  white-space: nowrap;
}

.link-invert {
  color: #fff;
  font-weight: 500;
  text-decoration: none;
  transition: all 0.3s;
}
.link-invert:hover {
  color: #fff;
  text-decoration: underline;
}

.iconsvg {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  vertical-align: middle;
}

.header_news_line {
  background: #6682d4 url("../img/bg/bg-news-line.jpg") no-repeat center/cover;
  color: #fff;
  padding: 1rem 0;
}
.header_news_line__container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header_news_line__txt {
  font-size: 13px;
}
@media (min-width: 767px) {
  .header_news_line__txt {
    font-size: 16px;
  }
}
.header_news_line__link {
  font-size: 12px;
  margin-left: 2.5rem;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  margin: var(--lg) 0 calc(2 * var(--lg)) 0;
}
.header__contacts {
  display: flex;
  margin-left: auto;
}
@media (max-width: 992px) {
  .header__contacts--addr {
    display: none;
  }
}
@media (min-width: 992px) {
  .header .contact:not(:last-child) {
    margin-right: var(--lg);
  }
}

.logo {
  margin: 0;
  flex: 0 0 250px;
  color: #6682d4;
  line-height: 1.2;
  text-decoration: none;
  transition: all 0.3s;
}
@media (max-width: 480px) {
  .logo {
    flex: 0 0 180px;
  }
}
.logo span {
  display: block;
}
.logo--header {
  font-size: 20px;
  margin-right: auto;
}

.section {
  margin-bottom: 100px;
}
@media (max-width: 1840px) {
  .section {
    margin-bottom: calc(
				60px + 40 *
					((100vw - 320px) / 1520)
			);
  }
}
.section--bg {
  padding: 100px;
  background-color: #eceef2;
}
@media (max-width: 1840px) {
  .section--bg {
    padding: calc(
				15px + 85 *
					((100vw - 320px) / 1520)
			);
  }
}
@media (max-width: 1024px) {
  .section--bg {
    margin-left: -15px;
    margin-right: -15px;
  }
}
.section--dark {
  background-color: #6682d4;
  color: #fff;
}

.title {
  font-family: "Prata", serif;
  font-weight: normal;
}

.title-section {
  margin: 0 0 var(--lg);
  font-size: 40px;
  font-family: "Prata", serif;
  line-height: 1.2;
  font-weight: 400;
  max-width: 650px;
}
@media (max-width: 540px) {
  .title-section {
    font-size: 28px;
  }
  .title-section__cnt {
    display: none;
  }
}
@media (max-width: 480px) {
  .title-section {
    font-size: 24px;
  }
}
.title-section--long {
  max-width: 900px;
}

.ico--round {
  position: relative;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  padding: 0.75em;
  color: #6682d4;
  background-color: #eceef2;
}
.ico--round .iconsvg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.light-txt {
  color: #9ba1b3;
}

.title-link {
  display: flex;
  align-items: flex-end;
  margin-bottom: calc(2 * var(--lg));
}
.title-link .title-section {
  margin-bottom: 0;
}

.more {
  color: #2f2c26;
  text-decoration: none;
  font-weight: 500;
}
.more--desktop {
  margin-left: auto;
  margin-bottom: 0.25em;
}
@media (max-width: 767px) {
  .more--desktop {
    display: none;
  }
}

.tarif {
  background: #6682d4 url("../img/bg/bg__tarif.png") no-repeat left center;
}

.tarif-list {
  display: grid;
  gap: var(--lg);
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 992px) {
  .tarif-list {
    grid-template-columns: repeat(4, 1fr);
  }
}
.tarif-list .service-card__txt {
  display: block;
}

.catalog-mini__cards {
  margin-top: 50px;
  display: grid;
}
@media (min-width: 540px) {
  .catalog-mini__cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .catalog-mini__cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.configurator {
  background: #eceef2 url("../img/bg/bg-configurator.png") no-repeat left top/cover;
}
.configurator__wrap {
  max-width: 600px;
}
.configurator__txt {
  margin: 1.75em 0 0;
  font-size: 18px;
}
.configurator__link {
  margin-top: 50px;
}
.configurator__link .btn {
  width: 320px;
}

.article-list {
  display: grid;
  gap: var(--lg);
}
@media (min-width: 767px) {
  .article-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .article-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.religions {
  background: #eceef2 url("../img/bg/bg_right.png") no-repeat right top;
}

.religions-list {
  display: grid;
  gap: var(--lg);
}
