  height: 16px;
  position: relative;
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.marker::before,
.marker::after {
  content: "";
  display: block;
  width: 16px;
  height: 2px;
  background: #2f2c26;
  position: absolute;
  pointer-events: all;
  transition: all 0.3s;
}

