.filter-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.filter-nav__item {
  margin-left: -1em;
  border-radius: 50px;
  border: 1px solid transparent;
}
.filter-nav__item.active {
  border-color: #5d78ca;
  background: #5d78ca;
}
.filter-nav__item:hover {
  border-color: #5d78ca;
}
.filter-nav__link {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  padding: 0.5em 1em;
}

.article-wrap {
  display: grid;
  gap: var(--lg);
}
@media (min-width: 767px) {
  .article-wrap {
    grid-template-columns: 350px 1fr;
  }
}

.catalog-cards {
  display: grid;
}
@media (min-width: 992px) {
  .catalog-cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

.catalog-more {
  border: 1px solid #E1E5F1;
  padding: 0.25em;
  text-align: center;
  margin-top: -1px;
  margin-left: -1px;
}

.product__status {
  border-bottom: 1px solid #E1E5F1;
  margin-bottom: var(--lg);
  padding-bottom: var(--lg);
  display: flex;
  align-items: center;
  gap: var(--lg);
}
.product__status .btn--title {
  margin: 0;
}
.product__vendor {
  margin-left: auto;
}
@media (max-width: 480px) {
  .product__vendor {
    font-size: 12px;
  }
}

.product-card {
  display: grid;
  gap: var(--lg);
  align-items: start;
}
@media (min-width: 992px) {
  .product-card {
    grid-template-columns: 1fr 550px;
  }
}
@media (min-width: 992px) {
  .product-card {
    grid-template-columns: 1fr 550px;
  }
}
.product-card__img {
  display: flex;
  gap: var(--lg);
  flex-direction: column-reverse;
}
@media (min-width: 1400px) {
  .product-card__img {
    flex-direction: row;
  }
  .product-card__img .product-slider-thumb {
    flex: 0 0 200px;
  }
}
.product-card__img .swiper-slide {
  border: 1px solid #E1E5F1;
}
.product-card__terms {
  font-weight: 500;
  text-decoration: none;
  color: #2f2c26;
}
.product-card__terms:hover {
  color: #6682d4;
}
.product-card__cost {
  display: flex;
  gap: 1em;
  justify-content: space-between;
  align-items: center;
  margin-top: var(--lg);
}
.product-card__price {
  font-size: 32px;
  font-family: "Prata", serif;
}
.product-card__btn {
  margin-top: var(--lg);
  display: flex;
  flex-direction: column;
  gap: 1em;
}
@media (min-width: 540px) {
  .product-card__btn {
    flex-direction: row;
  }
}
.product-card__btn .btn {
  height: 50px;
}
.product-card__btn .btn .phone {
  color: #6682d4;
  margin-right: 0.5em;
  font-size: 24px;
}
.product-card__spec {
  margin-top: calc(2 * var(--lg));
}

.spec__title {
  font-weight: 500;
  font-size: 22px;
  margin-bottom: 1em;
}
.spec__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.spec__item {
  display: flex;
  align-items: baseline;
}
.spec__item:not(:last-child) {
  margin: 0 0 1em;
}
.spec__item::before {
  content: "";
  border-bottom: 1px dotted;
  flex-grow: 1;
  order: 2;
  margin: 0 5px;
  order: 2;
}
.spec__item span {
  order: 3;
}

.product-slider-thumb {
  max-height: 500px;
}

.product-slider-main {
  position: relative;
}

.slider-zoom {
  font-size: 24px;
  position: absolute;
  left: var(--lg);
  top: var(--lg);
  z-index: 10;
  pointer-events: none;
}

.b-tooltip {
  border: 3px solid #fff;
  display: inline-block;
  font-size: 0.875em;
  padding: 0.75em;
  position: absolute;
  text-align: center;
}